const galleryImages = [
  { src: '/gallery/wedding1.jpg', alt: 'Wedding Event', type: 'Wedding' },
  { src: '/gallery/birthday1.jpg', alt: 'Birthday Event', type: 'Birthday' },
  { src: '/gallery/reception1.jpg', alt: 'Reception Event', type: 'Reception' },
  { src: '/gallery/corporate1.jpg', alt: 'Corporate Event', type: 'Corporate' },
  // Add more placeholder images as needed
];

export default function Gallery() {
  return (
    <section className="py-16 px-4 bg-[#23234a] text-cream">
      <h2 className="text-3xl md:text-4xl font-serif font-bold text-gold mb-10 text-center">Gallery</h2>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 max-w-6xl mx-auto">
        {galleryImages.map((img, idx) => (
          <div key={idx} className="rounded-xl overflow-hidden shadow-lg bg-[#1a1a2e]">
            <img
              src={img.src}
              alt={img.alt}
              className="w-full h-64 object-cover hover:scale-105 transition-transform duration-300"
              loading="lazy"
            />
            <div className="p-4 text-center">
              <span className="text-gold font-semibold">{img.type}</span>
            </div>
          </div>
        ))}
      </div>
      {/* Optional: Add event type filters and lightbox in future */}
    </section>
  );
}
