import Image from 'next/image';

export default function HeroSection() {
  return (
    <section className="relative flex items-center justify-center min-h-screen bg-gradient-to-br from-[#1a1a2e] via-[#2e2e4d] to-[#3a3a5c] text-gold">
      {/* Background Image Placeholder */}
      <div className="absolute inset-0 z-0">
        <Image
          src="/hero-placeholder.jpg"
          alt="Aashirvad Banquet Hall Venue"
          layout="fill"
          objectFit="cover"
          priority
          className="opacity-60"
        />
      </div>
      {/* Overlay */}
      <div className="relative z-10 flex flex-col items-center text-center px-4">
        <h1 className="text-5xl md:text-7xl font-serif font-bold text-gold drop-shadow-lg mb-4">Aashirvad Banquet Hall</h1>
        <p className="text-xl md:text-2xl text-cream font-light mb-6">Indian elegance, modern luxury, and traditional warmth in Nurpur, Himachal Pradesh</p>
        <a href="tel:+91XXXXXXXXXX" className="bg-gold text-[#1a1a2e] px-8 py-3 rounded-full shadow-lg font-semibold text-lg hover:bg-cream transition">Call to Book</a>
        <a href="mailto:contact@yourhall.com" className="mt-3 text-cream underline hover:text-gold">Email Us Today</a>
      </div>
    </section>
  );
}
